$inner_bevel_small_shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.15) inset,
  -2px -2px 4px 0px rgba(255, 255, 255, 0.85) inset,
  -2px -2px 6px 0px rgba(0, 0, 0, 0.15), 2px 2px 6px 0px #ffffffd9;
$dark-mode-bevel: -4px -4px 8px 0px rgba(0, 0, 0, 0.2),
  4px 4px 8px 0px rgba(0, 0, 0, 0.2) inset,
  -2px -2px 8px 0px rgba(255, 255, 255, 0.85) inset,
  2px 2px 8px 0px rgba(255, 255, 255, 0.85);
$inner_bevel_large_shadow: -4px -4px 8px 0px rgba(0, 0, 0, 0.2),
  4px 4px 8px 0px rgba(0, 0, 0, 0.2) inset,
  -4px -4px 8px 0px rgba(255, 255, 255, 0.85) inset,
  4px 4px 8px 0px rgba(255, 255, 255, 0.85);
$outer_bevel_small_shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.15),
  -2px -2px 4px 0px rgba(0, 0, 0, 0.15) inset,
  -2px -2px 6px 0px rgba(255, 255, 255, 0.85),
  2px 2px 4px 0px rgba(255, 255, 255, 0.85) inset;
$outer_bevel_large_shadow: 6px 6px 12px 0px rgba(0, 0, 0, 0.15),
  -6px -6px 12px 0px rgba(0, 0, 0, 0.15) inset,
  -6px -6px 12px 0px rgba(255, 255, 255, 0.85),
  6px 6px 12px 0px rgba(255, 255, 255, 0.85) inset;
